<template>
  <div class="">
    <div class="goods-list">
      <div class="top">
        <a check="true" class="changeicon all-icon"><img src="../../assets/img/full.png"></a>
        <h3>魅族</h3>
      </div>

      <div class="info clicker"  v-for="(item,index) in msg.img">
        <a check="true" class="changeicon"><img src="../../assets/img/full.png"></a>
        <dl>
          <dt>
            <img :src="item">
          </dt>
          <dd>
            <h3>{{msg.name[index]}}</h3>
            <p>{{msg.info[index]}}</p>
            <h3>{{msg.price[index]}}</h3>
          </dd>
        </dl>
        <div>
          <span class="less">-</span>
          <span>1</span>
          <span class="more">+</span>
        </div>
      </div>

    </div>
  </div>

</template>

<script>
    export default {
        name: "goodsCartList",
        props:['msg'],
        data(){
            return {
                // flag:false
            }
        },
        mounted() {
           this._changeImg();
           // 添加 加减事件
           this._multiple()
        },
        methods:{
            _changeImg(){
                // 点击更改背景图
                $('.changeicon').click(
                    function(){
                        // 改属性
                        var flag = $(this).attr('check');

                        if(flag == 'false'){
                          flag = 'true'
                        }else{
                          flag = 'false'
                        }
                        $(this).attr({"check":flag})
                        console.log(flag)

                        // 改图片
                        if(flag == 'false'){
                              $(this).find('img').attr({'src':'../../../static/empty.png'});
                        }else{
                              $(this).find('img').attr({'src':'../../../static/full.png'});
                        }

                    }
                );

                $('.top a').click(function () {
                    var flag = $(this).attr("check");

                    $('.changeicon').each(function(){
                        $(this).attr({"check":flag})
                        if(flag == 'false'){
                          $(this).find('img').attr({'src':'../../../static/empty.png'});
                        }else{
                          $(this).find('img').attr({'src':'../../../static/full.png'});
                        }
                    })
                })

                $('.goods-list .info a').click(function(){

                    // 改全选
                    var array = $('.goods-list .info .changeicon')

                    for(var i in array){
                        if(array.eq(i).attr('check') == 'false'){

                            $('.top a').attr({'check':'false'});
                            $('.top a').find('img').attr({'src':'../../../static/empty.png'});
                            return;
                        }
                    }

                    console.log('check all is true')
                    $('.top a').attr({'check':'true'});
                    $('.top a').find('img').attr({'src':'../../../static/full.png'});

                })
            },
            _multiple(){
              // console.log($('.less'))
                $('.less').click(function(){
                    var $numDom = $(this).next()
                    var num = $numDom.text();
                    if(num <=1){
                      return;
                    }
                    num --;
                    $numDom.text(num);
                })
              $('.more').click(function(){
                var $numDom = $(this).prev()
                var num = $numDom.text();
                num ++;
                $numDom.text(num);
              })
            }
        }

    }
</script>

<style scoped>
.goods-list{
    background-color: #fff;
    padding:0 .24rem;
    display: flex;
    flex-direction: column;
    margin-top: .18rem;
}
.goods-list>.top{
    height:.67rem;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    align-items: center;

}
.goods-list>.top>a{
  width:.33rem;
  height:.33rem;
}

.goods-list>.top>a>img{
  width:100%;
  height:100%;
}


.goods-list>.top>h3{
    font-size:.2rem;
    color:#999;
    padding-left:.5rem;
}
.goods-list>.info {
  height: 1.5rem;
  padding: .26rem 0 .23rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.goods-list>.info>a{
  width:.33rem;
  height:.33rem;
}

.goods-list>.info>a>img{
  width:100%;
  height:100%;
}

.goods-list>.info>dl {
  display: flex;
}
.goods-list>.info>dl>dt{
    width:.81rem;
    height:1rem;
}
.goods-list>.info>dl>dt>img{
    width:100%;
    height:100%;
}

.goods-list>.info>dl>dd{
    padding-left:.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.goods-list>.info>dl>dd>h3:nth-of-type(1){
      font-size: .22rem;
      color:#333;
}
.goods-list>.info>dl>dd>p{
  font-size: .17rem;
  color:#ccc;
  white-space: nowrap;
}
.goods-list>.info>dl>dd>h3:nth-of-type(2){
    font-size:.18rem;
    color:#ff3366;

}
.goods-list>.info>div{
    height:100%;
  display: flex;
  align-items: flex-end;
  position:relative;
  margin-left: -.2rem;
}
.goods-list>.info>div>span[class]{
  /*position:absolute;*/
  /*bottom: 0;*/
  /*font-size: .4rem;*/
  /*line-height: .24rem;*/
}
/*.goods-list>.info>div>span.less{*/
  /*left: -.2rem;*/
/*}*/
/*.goods-list>.info>div>span.more{*/
  /*right: -.2rem;*/
/*}*/

.goods-list>.info>div>span{
    /*display: flex;*/
    /*flex-direction: column;*/
    /*justify-content: flex-end;*/
    /*height:100%; */
    width: .3rem;
    line-height: .3rem;
    border: 1px solid #eee;
    text-align: center;
}


</style>



