<template>
    <div class="img-show">
        <div>
            <img src="../../../static/gd1.png">
        </div>
        <div>
            <img src="../../../static/gd2.png">
        </div>
    </div>
</template>

<script>
    export default {
        name: "goodsImg"
    }
</script>

<style scoped>
.img-show{
    display: flex;
    width:100%;
    flex-direction: column;
}
.img-show>div{
    width: 100%;
}
.img-show>div:nth-of-type(1){
    width:100%;
}
.img-show>div:nth-of-type(1)>img{
    width: 100%;
    height:auto;
}
.img-show>div:nth-of-type(2){
    width:100%;
}
.img-show>div:nth-of-type(2)>img{
    width: 100%;
    height:auto;
}
</style>



